/* eslint-disable no-console,func-names,react/no-multi-comp */
import React from 'react';
import ReactDOM from 'react-dom';
import Table from 'rc-table';
import 'rc-table/assets/index.less';
import 'rc-table/assets/animation.less';

const createDate = () => {
  let data = [];
  for (let a = 0; a < 10; a++) {
    data.push({
      key: "" + a,
      name: "" + a,
      age: "32",
      address: "London, Park Lane no. 0",
      children: []
    });
  }
  return data;
};

class EditableTable extends React.Component {
  state = {
    dataSource: createDate(),
    expandedRowKeys: []
  };
  constructor(props) {
    super(props);
    this.columns = [
      {
        title: "name",
        dataIndex: "name",
        width: "30%"
      },
      {
        title: "age",
        dataIndex: "age"
      },
      {
        title: "address",
        dataIndex: "address"
      }
    ];
  }

  onExpandedRowsChange = expandedRowKeys => {
    //
    console.log("onExpandedRowsChange", expandedRowKeys);
    this.setState({ expandedRowKeys });
  };

  setExpandedRowKeys = () => {
    this.init();
    // 这之后不知道为啥居然调用 onExpandedRowsChange
    this.setState({ expandedRowKeys: ["0"] });
  };

  init = () => {
    this.setState({
      dataSource: [
        {
          key: "0",
          name: "1(应该展开)",
          age: "32",
          address: "London, Park Lane no. 0",
          children: [
            {
              key: "0-1",
              name: "0-1",
              age: "32",
              address: "London, Park Lane no. 0"
            }
          ]
        }
        // 将下方解除注释就正常
        // {
        //   key: "1",
        //   name: "2",
        //   age: "32",
        //   address: "London, Park Lane no. 1",
        //   children: []
        // }
      ]
    });
  };

  render() {
    const { dataSource } = this.state;

    const columns = this.columns;
    return (
      <div>
        <button
          onClick={this.setExpandedRowKeys}
          type="primary"
          style={{ marginBottom: 16 }}
        >
          加载数据并展开
        </button>
        <Table
          bordered
          expandedRowKeys={this.state.expandedRowKeys}
          onExpandedRowsChange={this.onExpandedRowsChange}
          data={dataSource}
          columns={columns}
        />
      </div>
    );
  }
}
ReactDOM.render(<EditableTable />, document.getElementById('__react-content'));



